import React, { useState } from 'react'
import Text from '../../../components/Text'
import Box from '../../../components/Box'
import Image from '../../../components/Image'
import PageModule from '../../../components/PageModule'
import { responsive } from '../../../components/ThemeProvider/theme'

import circle from '../circle.svg'
import useResponsive from '../../../contexts/mediaQuery/useResponsive'
import Carousel from '../../../components/Carousel'
import LeftTitle from '../../../components/LeftTitle'

const circleData = [
  {
    width: '18.4%',
    minWidth: '260px',
    top: '15.1%',
    right: '27.8%',
    titleText: (
      <>
        抵用金額<br/>
        <Text.Inline color="dance">2118萬元</Text.Inline>
      </>
    ),
    description: [
      '佔藝FUN券發行總金額\n約1%（註1）',
    ],
  },
  {
    width: '18.4%',
    minWidth: '260px',
    top: '55%',
    right: '21.5%',
    titleText: (
        <>
          訂單金額<br/>
          <Text.Inline color="dance">4472萬元</Text.Inline>
        </>
    ),
    description: [
      '佔兩廳院售票平台\n同期票房約5%（註2）',
    ]
  },
  {
    width: '18.4%',
    minWidth: '260px',
    top: '27.4%',
    right: '5.5%',
    titleText: (
        <>
          購票會員人數<br/>
          <Text.Inline color="dance">3.3萬人</Text.Inline>
        </>
    ),
    description: [
      '佔兩廳院售票平台同期購票\n會員數約23%（註3）',
    ]
  },
]

const CircleModule = ({ width, top, right, titleText, description, ...props}) => (
  <Box.Absolute width={width} top={top} right={right} {...props}>
    <Image src={circle} />
    <Box.AbsCenter width="100%" textAlign="center">
      <Text.H4 whiteSpace="pre-wrap">
        {titleText}
      </Text.H4>
      <Box.Inline>
        {description.map((d, k) => (
          <Text fontSize={responsive('0.75em', '1em')} mt="0.625rem" whiteSpace="pre-wrap" fontWeight="400" textAlign="left" key={k}>
            {d}
          </Text>
        ))}
      </Box.Inline>
    </Box.AbsCenter>
  </Box.Absolute>
)

const notes = [
  '註1：文化部發行數位、紙本藝FUN券共約15.6億。',
  '註2：兩廳院售票平台同期票房約8.4億，同期時間對照藝FUN券使用期間2020.7.22~2021.2.28。',
  '註3：兩廳院售票平台同期購票會員人數約14萬人，同期時間對照藝FUN券使用期間2020.7.22~2021.2.28。'
]

const Page = () => {
  const { isMobile } = useResponsive()
  const [ noteIndex, setNote ] = useState(0)
  return (
    <PageModule
      left={
        <LeftTitle
          smallTilte="2020疫情之下"
          title="藝文振興啟動"
          description="2020年下半年疫情趨緩，公眾活動容留人數鬆綁，繼行政院振興3倍券，文化部為振興藝文產業、鼓勵民眾參與藝文活動，發行數位、紙本藝FUN券(共15.6億)，望能振興藝文產業。"
          note={!isMobile && '註1：文化部發行數位、紙本藝FUN券共約15.6億。\n註2：兩廳院售票平台同期票房約8.4億，同期時間對照藝FUN券使用期間2020.7.22~2021.2.28。\n註3：兩廳院售票平台同期購票會員人數約14萬人，同期時間對照藝FUN券使用期間2020.7.22~2021.2.28。'}
          multiple={2}
        />
      }
      right={
        <>
          {isMobile ? (
            <Box>
              <Carousel
                width="76%"
                mx="auto"
                slidesToShow={1}
                typeIndex={noteIndex}
                setType={setNote}
              >
                {circleData.map(({ titleText, description, mobileDescription }, i) => (
                  <CircleModule
                    titleText={titleText}
                    description={mobileDescription || description}
                    position="relative"
                    key={i}
                  />
                ))}
              </Carousel>
              <Text fontSize="0.75em" >{notes[noteIndex]}</Text>
            </Box>
          ) : circleData.map(({ width, top, right, titleText, description, minWidth }, i) => (
            <CircleModule
              width={width}
              minWidth={minWidth}
              top={top}
              right={right}
              titleText={titleText}
              description={description}
              key={i}
            />
          ))}
        </>
      }
      rightMarginTop="0em"
    />
  )
}

export default Page
